  .products__btn {
    margin: 35px 25px;
    margin-top: 0;
  }
}
.products__btn:focus, .products__btn:hover {
  color: #002b66;
  background-color: #d8d8d9;
  opacity: 0.8;
}
.products__btn:active {
  opacity: 1;
}

